#wp-graphiql-wrapper .docExplorerWrap {
  background: white;
  box-shadow: 0 0 8px rgb(0 0 0 / 15%);
  position: relative;
  z-index: 4;
}

#wp-graphiql-wrapper .docExplorerWrap .doc-explorer-title-bar {
  cursor: default;
  display: flex;
  height: 34px;
  line-height: 14px;
  padding: 8px 8px 5px;
  position: relative;
  -webkit-user-select: none;
  user-select: none;
}

#wp-graphiql-wrapper .docExplorerWrap .doc-explorer-title {
  flex: 1 1;
  font-weight: 700;
  overflow-x: hidden;
  overflow-y: hidden;
  padding: 5px 0 10px 10px;
  text-align: center;
  text-overflow: ellipsis;
  -webkit-user-select: text;
  user-select: text;
  white-space: nowrap;
}

#wp-graphiql-wrapper .docExplorerWrap .doc-explorer-rhs {
  position: relative;
}
